body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: 'Poppins', sans-serif;
}

.nav-link {
    color: #070D43;
}


.btn--large{
    color: #fff;
    padding: .6rem 2rem;
    font-size: .9rem;
}

.navbar-toggler {
    border: none !important;
    box-shadow: none !important;
}

.fs-7 {
    font-size: .85rem;
}

.intro-container {
    border-radius: 2rem;
    background-color: #D3155F;
    color: #fff;
}

.intro-container > h1 {
    
    font-size: 2rem;
}

.left-content-heading {
    font-weight: 700;
}

.left-content-subtext{
    font-size: 1rem;
    font-weight: 400;
    font-family: 'Didact Gothic', sans-serif;
    /* margin-top: 1rem; */
}

.section__title {
    text-align: center;
    font-size: 1.3rem;
    font-family: 'Poppins', sans-serif !important;
    font-weight: 700;
}

.steps__number {
    position: relative;
    display: flex;
    justify-content: center;
    background-color: #24D315;
    width: 2.5rem;
    height: 2.5rem;
}

.steps__number > span {
    position: absolute;
    align-self: center;
    color: #fff;
}

.step__title {
    font-size:  1rem;
    font-weight: 700;
}

.step__description {
    color: #434343;
    font-family: 'Didact Gothic';
}

.articles__label {
    background-color: #1AA9E8;
    color: #FFF;
    padding: 5px 12px;
    font-size: .8rem;
    border-radius: .4rem;
}
.artciles__title {
    font-weight: 600;
    line-height: 2.4rem;
}

.articles__description {
    color: #434343;
    line-height: 1.7rem;
}
/* Responsive */
@media screen and (max-width: 600px) {
    .intro-container{
        border-bottom-left-radius: 0rem;
        border-bottom-right-radius: 0rem;
    }
  }
.footer {
    background-color: #070D43;
}

.org__description {
    color: #fff;
    font-weight: 300;
    font-size: .86rem;
    line-height: 1.4rem;
}

.footer__title {
    color: #fff;
    font-weight: 600;
}

.footer_links{
    list-style: none;
    padding: 0;
}

.footer__link > a{
    text-decoration: none;
    color: #D0D0D0;
    font-size: .9rem;
    font-weight: 300;
}

.line {
    color: #fff;
    background-color: #fff;
}

.copyright__content {
    color: #fff;
    font-size: .8rem;
}
.list-group-own{
    list-style: none;
    display: flex;
    margin-top: 0 !important;
}

.list-group-item-own, .list-group-item-own > a{
    padding-left: 30px;
    text-decoration: none;
    color: #000;
    font-size: .9rem;
    font-weight: 500;
}
.sigup__header > img {
    width: 6rem;
    height: 2.4rem;
}

.input-group {
    outline: none;
}

.info {
    font-size: .8rem;
    color: #808080;
}

.agreement{
    text-align: center;
    font-size: .85rem;
}

.error-container > span {
    color: red;
}
